/*
 *  Newsletter
 *    Styles for the newsletter section.
 *
 *  Version:
 *    1.0.0 - 2021/10/23
 */

.c-newsletter {
  position: relative;
  padding: 2.25rem 0 2.375rem;
  // margin: -2.25rem 0 -2.375rem;
  // mask-image: url(../../assets/svg.shape-8.svg);
  // mask-repeat: no-repeat;
  // mask-position: center;
  // mask-size: 100%;
  color: $s-color--font-invert;
  background-color: $s-color--background-impact;
  overflow: hidden;

  // Container
  &__container {
    position: relative;
    max-width: 23.4375rem;
    padding: 5.625rem 0 6.75rem;
    margin: 0 auto;
    z-index: $s-z--text-overlay;
  }

  // Background Illustration
  &__illustration-bg {
    &.is-desktop {
      display: none;
    }

    &-wrapper {
      @include t-absolute-center-x;
      top: 0;
      width: 25.375rem;
      z-index: $s-z--text-overlay;
    }

    &-container {
      padding-bottom: 87.93%;
    }
  }

  // Illustration 1
  &__illustration-1 {
    &-wrapper {
      position: absolute;
      top: 0.375rem;
      right: -1.5rem;
      width: 7.9375rem;
      z-index: $s-z--illustration-overlay;
    }

    &-container {
      padding-bottom: 80%;
    }
  }

  // Illustration 2
  &__illustration-2 {
    &.is-desktop {
      display: none;
    }

    &-wrapper {
      position: absolute;
      bottom: -0.125rem;
      left: 1.0625rem;
      width: 8.75rem;
      z-index: $s-z--illustration-overlay;
    }

    &-container {
      padding-bottom: 68.57%;
    }
  }

  // Illustration 3
  &__illustration-3 {
    &-wrapper {
      position: absolute;
      top: 5.25rem;
      right: -1.25rem;
      width: 34.3125rem;
      z-index: $s-z--reset;
    }

    &-container {
      padding-bottom: 68.12%;
    }
  }

  // Illustration 4
  &__illustration-4 {
    &-wrapper {
      position: absolute;
      top: -4.625rem;
      right: calc(100% - 0.5rem);
      width: 39.4375rem;
      z-index: $s-z--reset;
    }

    &-container {
      padding-bottom: 97.81%;
    }
  }

  &__illustration-3-wrapper,
  &__illustration-4-wrapper {
    display: none;
  }

  // Title
  &__title {
    position: relative;
    margin-bottom: 1.625rem;
    text-align: center;
    z-index: $s-z--illustration-overlay;
  }

  // Form
  &__form {
    position: relative;
    max-width: 23.4375rem;
    padding: 0 3.75rem;
    margin: 0 auto;
    text-align: center;
    z-index: $s-z--illustration-overlay;

    .o-input.is-secondary {
      color: $s-color--font-invert;
      border-color: $s-color--border-invert;
      background: transparent;

      &::placeholder{
        color: rgba($s-color--font-invert,.4);
      }
      
    }

    .c-newsletter-form {
      &__subtitle {
        margin-bottom: 1.625rem;
      }

      &__input-container {
        position: relative;
      }

      &__btn {
        width: 1.125rem;
        background: url(../../assets/svg.arrow-right.svg) no-repeat -0.0625rem center/100%;
      }
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    padding: 2.1875rem 0 3.375rem;

    &__container {
      max-width: 49.5625rem;
      padding: 10.75rem 6.6875rem;
    }

    .c-newsletter__illustration-1-wrapper {
      top: 0;
      right: -12.5rem;
      width: 20.2375rem;
    }

    .c-newsletter__illustration-2-wrapper {
      top: 1.8125rem;
      bottom: initial;
      left: -13.875rem;
      width: 21.5rem;
    }

    .c-newsletter__illustration-3 {
      &-wrapper {
        display: block;
        top: 2.6875rem;
        right: -28.375rem;
        width: 45rem;
      }
    }

    .c-newsletter__illustration-4 {
      &-wrapper {
        display: block;
        top: 0;
        left: -11rem;
        width: 31.25rem;
      }
    }

    &__illustration-bg {
      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }

      &-wrapper {
        width: 49.5625rem;
      }

      &-container {
        padding-bottom: 65.6%;
      }
    }

    &__illustration-1,
    &__illustration-2 {
      &.is-mobile {
        display: none;
      }

      &.is-desktop {
        display: block;
      }
    }

    &__title {
      margin-bottom: 1.125rem;
    }

    &__form {
      padding: 0;
      max-width: 49.5625rem;

      .c-newsletter-form {
        &__subtitle {
          margin-bottom: 3.0625rem;
          text-align: center;
        }

        &__input-container {
          margin: 0 0.5938rem;
        }

        &__btn {
          width: 1.3125rem;
          background-position: 0 center;

          &:hover{
            outline: none;
          }
        }
      }
    }
  }
  @include t-media-query($s-large-up) {
    .c-newsletter__illustration-4 {
      &-wrapper {
        display: block;
        top: 1.375rem;
        left: -15rem;
        width: 35rem
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 7.1875rem 0 5.375rem;

    .c-newsletter__illustration-4 {
      &-wrapper {
        display: block;
        top: -5rem;
        left: -29rem;
        width: 39.0625rem;
      }
    }
  }
}