/*
 *  Newsletter Form
 *    Styles for the newsletter form
 *
 *  Version:
 *    1.0.0 - 2021/02/26
 */

.c-newsletter-form {
  position: relative;

  &__btn {
    @include t-absolute-center-y;
    right: 0.625rem;
    width: 1.5625rem;
    height: 100%;
    font-size: 0;
    background: url(../../assets/icon-arrow-right.svg) no-repeat right center/1.0313rem;
    cursor: pointer;
    transition: transform .2s;

    &:hover {
      transform: translate(0.4375rem, -50%);
    }
  }

  &__success {
    color: $s-color--font-focus;
  }

  &__input.is-secondary {
    padding-left: 0.625rem;
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    &__btn {
      background-size: 1.375rem;
      right: 1rem;

      &:hover {
        transform: translate(0.625rem, -50%);
      }
    }

    &__input.is-secondary {
      padding-left: 1rem;
    }

  }
}